<template>
  <!-- 企业设置 -->

  <div>
    <div v-if="$route.name == 'passwordIntensity'">
      <mrc_ess_skLoaders :skLoading='skLoading'>
        <v-card>
          <v-card-text>
            <parent-company v-for="item in companyList"
                            :key='item.uuid'
                            @delData='delData'
                            :companyObj='item' />
          </v-card-text>
        </v-card>
      </mrc_ess_skLoaders>
    </div>
    <router-view v-else></router-view>
  </div>

</template>

<script>
import { get_company_list, get_company_del } from '@/service/api/company_set'
import parentCompany from './components/parentCompany'
export default {
  components: { parentCompany },
  props: {},
  data () {
    return {
      companyList: [],
      skLoading: false
    };
  },
  created () { },
  async mounted () {
    if (this.$route.name == 'passwordIntensity') {
      await this.getList()
      this.skLoading = false
    }
  },
  methods: {
    delData (uuid) {
      this.Recursion(this.companyList, uuid)
    },
    Recursion (arr, uuid) {
      if (arr.length <= 0) return ''
      arr.forEach((item, index) => {
        if (item.uuid == uuid) {
          arr.splice(index, 1)
        } else {
          if (item.children) {
            this.Recursion(item.children, uuid)
          }

        }
      })
    },
    // 获取公司列表
    async getList () {
      this.skLoading = true
      const list = await get_company_list({ client_uuid: this.$store.state.currentCompanyClientUuid })
      if (list.code == 200) {
        this.companyList = list.data
      }
      this.skLoading = false
    },
  },
  computed: {},
  watch: {
    async '$route' (val) {
      if (val.name == 'passwordIntensity') {
        await this.getList()
        this.$store.commit('setTab', 0)
      }

    }
  },
};
</script>

